/* Scss Document */

@charset "utf-8";
@import "./_grid";
.o-inputcontrol {
    height: 60px;
    font-size: $font13;
    display: block;
    padding: 3.0px .75rem;
    line-height: 1.0;
    background-color: #fff;
    background-image: none;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, .15);
    border-radius: .25rem;
    transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    &:focus {
        border-color: $lightblue;
    }
    &[extend="visibility"] {
        border-right: none;
    }
}

.o-inputaddon {
    padding: 3.0px .75rem;
    margin-bottom: 0;
    font-weight: 400;
    line-height: 1.0;
    color: $lightgrey--light;
    text-align: center;
    background-color: #eceeef;
    border: 1px solid rgba(0, 0, 0, .15);
    border-radius: .25rem;
    cursor: pointer;
}

.o-inputcontrol.o-padding--register{
    padding: 2.0px;
}
.o-inputaddon.o-padding--register{
    padding: 2.0px;
}

.o-inputaddon.o-password {
    /*padding: .5rem .75rem;*/
    margin-bottom: 0;
    font-weight: 400;
    line-height: 60px;
    text-align: center;
    background-color: $white;
    border: 1px solid rgba(0, 0, 0, .15);
    border-radius: .25rem;
    cursor: pointer;
}

.o-inputlabel {
    color: $lightgrey--light;
}

.o-inputerror {
    font-size: $font13;
}

.o-inputlabelgroup {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 100%;
}

.o-inputedlabel {
    font-size: $font22;
    word-break: break-all;
}

.o-inputgrouprow {
    display: flex;
    flex-direction: row;
    & .o-inputgroup--column+.o-inputgroup--column {
        margin-left: 20px;
    }
}

@mixin o-inputgroup {
    position: relative;
    display: flex;
    width: 100%;
    & .o-inputcontrol {
        position: relative;
        z-index: 2;
        margin-bottom: 0;
        box-sizing: border-box;
        width: 100%;
    }
    & .o-inputcontrol+.o-inputaddon:not(:first-child) {
        border-left: 0;
    }
    & .o-inputaddon {
        &:not(:first-child) {
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
        }
    }
}

.o-inputgroup {
    @include o-inputgroup;
    flex-direction: row;
    & .o-inputcontrol {
        flex: 1 1 auto;
        &:not(:last-child) {
            border-bottom-right-radius: 0;
            border-top-right-radius: 0;
        }
    }
}

.o-inputgroup--column {
    @include o-inputgroup;
    flex-direction: column;
    & .o-inputcontrol {
        flex: 0 1 auto;
    }
    & .o-inputerror {
        visibility: hidden;
    }
    & .mdl-textfield__guide {
        margin-top: 5px;
    }
    &:not(.is-invalid) + .o-inputgroup--column:not(.is-invalid) > input{
        border-top-style: none;
        &:focus {
            border-top-style: solid;
        }
    }
}

.o-inputgroup--column.is-invalid {
    & .o-inputlabel {
    }
    & .o-inputcontrol {
        border-color: $red;
    }
    & .o-inputerror {
        color: $red;
        visibility: visible;
    }
}

.o-inputgrouprow .o-inputgroup--column:not(.is-invalid) + .o-inputgroup--column:not(.is-invalid) > input{
    border-top-style: solid;
}

.o-input__post {
    &--1,
    &--2 {
        width: 150px;
    }
}

.o-input__tel {
    &--1,
    &--2,
    &--3 {
        width: 150px;
        min-width: 100px;
    }
}

.o-input__fullname {
    &--1,
    &--2,
    &--3 {
        width: 220px;
    }
}

.o-input__nickname {
    width: 450px;
}

// brekpoint sample
////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .o-inputedlabel,
    .o-inputerror,
    .o-inputlabel {
        font-size: $font13;
    }
    .o-input__tel {
        &--1,
        &--2,
        &--3 {
            min-width: 95px;
        }
    }
}
    
////////// Tablet //////////
@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {
    .o-inputedlabel,
    .o-inputerror,
    .o-inputlabel {
        font-size: $font13;
    }
    .o-input__tel {
        &--1,
        &--2,
        &--3 {
            min-width: 95px;
        }
    }
}

////////// Desktop //////////
@media (min-width: $grid-desktop-breakpoint) and (max-width: $grid-menudesktop-breakpoint - 1) {
    .body.is-sidebar-show {
        .o-inputedlabel,
        .o-inputerror,
        .o-inputlabel {
            font-size: $font13;
        }
        
        .o-input__tel {
            &--1,
            &--2,
            &--3 {
                min-width: 135px;
            }
        }
    }
}
