/* Scss Document */

@charset "utf-8";

.footer{
    padding-top: 13px;
    &__nav {
        display: flex;
        flex-direction: row;
        align-items: center;
        list-style: none;
        padding-right: 40px;
        margin-bottom: 0px;
        flex-wrap: wrap;
        & .navitem:not(.secure){
            flex-shrink: 0;
        }
    }
    & .o-link--sm {
            color: $lightgrey;
    }
    & .secure {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-left: auto;
        & .secure__img {
            margin-left: 13px;
            width: 107.5px;
            height: 43px;
        }
        & .secure__message {
            font-size: $font13;
            & > a {
                text-decoration: underline;
                color: $yblue;
            }
        }
    }
    
    &__copyright{
        text-align: right;
        font-size: 13px;
        margin-bottom: 13px;
        color: #BDBDBD;
        & > div{
            margin-right: 24px;    
        }
    }
}

// brekpoint sample
////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .footer{
        &__nav {
            padding-right : 12px;
            padding-left : 12px;
            justify-content: center;
        }
        &__copyright{
            text-align: center;
            & > div{
                margin-right: auto;    
                margin-left: auto; 
            }
        }
    }
}


////////// Tablet //////////
@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {
    .footer{
        &__nav {
            padding-right : 12px;
            padding-left : 12px;
            justify-content: center;
        }        
        &__copyright{
            text-align: center;
            & > div{
                margin-right: auto;    
                margin-left: auto; 
            }
        }
    }
}

