/* Scss Document */

@charset "utf-8";
$white: #FFFFFF;
$black: #000000;
$yblue: #005bac;
$yblue--special: rgba(0 , 91, 172, 0.75);
$lightblue: #0FA9F4;
//$lightblue--light: #90CAF9;
$lightblue--light: #B3E5FC;
$blue: #2196F3;
$blue--light: #BBDEFB;
$blue--dark: #1976D2;
$dullblue: #EFF2F6;
$puregrey: #FAFAFA;
$lightgrey: #757575;
$lightgrey--light: #BDBDBD;
$darkgrey: #616161;
$red: #F44336;
$red--light: #FFCDD2;
$yellow: #FFEB3B;
$yellow--light: #FFF9C4;
$orange: #FF9800;
$orange--light: #FFE0B2;
$green: #4CAF50;
$green--light: #C8E6C9;
$kaikeiol:#0092D8;
$meisaiol:#6FBA2C;
$shirool:#37BEF0;
$aool:#00AFEC;
$misoca:#00ACC1;

.o-fontc--darkgrey {
    color: $darkgrey;
}
.o-fontc--lightgrey {
    color: $lightgrey;
}
.o-fontc--lightblue {
    color: $lightblue;
}

.o-fontc--yblue {
    color: $yblue;
}

.o-fontc--ybluespecial {
    color: $yblue--special;
}

.o-fontc--white {
    color: $white;
}

.o-font--red {
    color: $red;
}

.o-font--kaikeiol {
    color: $kaikeiol;
}

.o-font--meisaiol {
    color: $meisaiol;
}

.o-font--shirool {
    color: $shirool;
}

.o-font--aool {
    color: $aool;
}

.o-font--misoca {
    color: $misoca;
}

.o-bgcolor-kaikeiol {
    background-color: $kaikeiol;
}

.o-bgcolor-meisaiol {
    background-color: $meisaiol;
}

.o-bgcolor-shirool {
    background-color: $shirool;
}

.o-bgcolor-aool {
    background-color: $aool;
}

.o-bgcolor-misoca {
    background-color: $misoca;
}