/* Scss Document */

@charset "utf-8";

/*
 * sidebar
 */


/* sidebar navigation */

.sidebar {
    height: 100%;
    width:  300px;
    padding-top: 40px;
    overflow-y: hidden;
    background-color: $puregrey;
    & a{
       &.o-linkdarkgrey--sm , &.o-linkdarkgrey--md , &.o-linklightblue--sm{
        font-weight: 400; 
       }
    }
    &::-webkit-scrollbar{
        width: 10px;
    }
    &::-webkit-scrollbar-track{
        background: #dddddd;
    }
    &::-webkit-scrollbar-thumb{
        background: $yblue;
    }
    
    & ul {
        margin-bottom: 0px;
        padding-top: 0px;
        padding-bottom: 0px;
    }
    & .sidebar__nav {
        width: 100%;
        & i {
            padding-top: 1px;
        }
        & li {
            padding-left: 21px;
            padding-top: 12px;
            padding-bottom: 12px;
            border-left-color: $puregrey;
            border-width: 3px;
            border-left-style: solid;
            color: $darkgrey;
            &.is-active,
            &.is-active .material-icons,
            &.is-active a
             {
                color: $yblue;
                cursor: default;
            }
            &.is-active[for] a{
                font-weight: bold;
            }
            &.is-active:not([for]){
                background-color: $dullblue;
                border-left-color: $yblue;
            }
            &:not(.is-active):not(.is-fixed):hover,
            &:not(.is-active):not(.is-fixed):hover .material-icons,
            &:not(.is-active):not(.is-fixed):hover a,
            &.is-active[for]:not(.is-fixed) a{
                color: $blue--dark;
                cursor: pointer;
            }
        }
        & .mdl-list__item[for] {
            margin-left: 57px;
            font-size: $font13;
            min-height: auto;
            max-height: 40px;
            padding-left: 12px;
            padding-right: 0px;
        }
        & .mdl-list__item{
            & .o-iconlink{
                width: 100%;
                & i{
                    font-size: $font22;
                }
                &.o-linklightblue--sm{
                    & span{
                        width: 189px;
                    }
                }
            }
        }
        & .sidebar__navitem--moveservice {
            margin-left: 57px;
            font-size: $font13;
            & .material-icons {
                color: $lightblue;
            }
        }
    }
    &:focus,
    &:hover {
        overflow-y: auto;
    }
    
    & .mdl-list__item .mdl-list__item-primary-content .mdl-list__item-icon {
        margin-right: 24px;
    }
}

@-moz-document url-prefix() {
    .sidebar {
        & .sidebar__nav {
            & .mdl-list__item{
                & .o-iconlink{
                    &.o-linklightblue--sm{
                        & span{
                            line-height: 21px;
                        }
                    }
                }
            }            
        }
    }
}