/* Scss Document */

@charset "utf-8";

@mixin o-linkblue {
    cursor: pointer;
    color: $yblue;
    &:hover {
        text-decoration: none;
    }
    &:focus {
        text-decoration: none;
    }
}

@mixin o-messagelinkblue {
    cursor: pointer;
    color: $yblue;
    text-decoration: underline;
}

@mixin o-linklightblue {
    cursor: pointer;
    color: $lightblue;
    &:hover {
        text-decoration: none;
    }
    &:focus {
        text-decoration: none;
    }
}

@mixin o-linkdarkgrey {
    cursor: pointer;
    color: $darkgrey;
    &:hover {
        text-decoration: none;
    }
    &:focus {
        text-decoration: none;
    }
}

@mixin o-linkgrey {
    cursor: pointer;
    color: $lightgrey;
    &:hover {
        text-decoration: none;
    }
    &:focus {
        text-decoration: none;
    }
}

.o-linkblue {
    &--sm {
        @include o-linkblue;
        font-size: $font13;
    }
    &--md {
        @include o-linkblue;
        font-size: $font15;
    }
    &--lg {
        @include o-linkblue;
        font-size: $font17;
    }
}

