/* Scss Document */

@charset "utf-8";


/* Move down content because we have a fixed navbar that is 50px tall */
html{
    height: auto;
}

body {
    height: 100%;
}

body {
    /*padding-top: 50px;*/
    background-color: $puregrey;
    letter-spacing: 1px;
    font-family: 'Noto Sans Japanese', sans-serif;
}

span,
button,
input {
    font-family: 'Noto Sans Japanese', sans-serif;
}

a {
    font-family: 'Noto Sans Japanese',sans-serif;
    text-decoration: none;
    cursor: pointer;
    color : $yblue;
    &:hover {
        color: $blue--dark;
    }
}

a > div:hover {
    color: $blue--dark;
}

dl,
ol,
ul {
    margin-top: 0;
}

.bg-color-yblue {
    background-color: $yblue;
}