/* Scss Document */

@charset "utf-8";

@import "../../object/component/_grid";

.o-title {
    &--sm {
        font-size: $font15;
        color: $darkgrey;
    }
    &--md {
        font-size: $font17;
        color: $darkgrey;
    }
    &--lg {
        font-size: $font22;
        color: $darkgrey;
    }
}


// brekpoint sample
////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .o-title {
        &--sm {
            font-size: $font13;
        }
        &--md {
            font-size: $font15;
        }
        &--lg {
            font-size: $font20;
        }
    }    
}


////////// Tablet //////////
@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {
    .o-title {
        &--sm {
            font-size: $font13;
        }
        &--md {
            font-size: $font15;
        }
        &--lg {
            font-size: $font20;
        }
    }    
}

