/* Scss Document */

@charset "utf-8";
.header {
    display: flex;
    flex-direction: row;
    border-top-style: solid;
    border-top-color: $yblue;
    border-top-width: 3px;
    background-color: $puregrey;
    min-height: 56px;
    flex-wrap: wrap;
    align-items: center;
    &__menu {
        border-radius: 50%;
        margin-left: 19px;
        margin-right: 14px;
        width: 32px;
        &>i {
            color: $darkgrey;
        }
    }
    &__logo {
        //        position: absolute;
        width: 121px;
        height: 48px;
//        top: 7px;
//        left: 80px;
        &.is-search {
            display: none;
        }
    }
    &__servicename {
        width: 180px;
        height: 30px;
        margin-left: 12px;
        margin-right: 12px;
        &.is-search {
            display: none;
        }
    }
    &__search {
        margin: auto;
        margin-right: 12px;
        flex-grow: 1;
        & .o-inputgroup{
            max-width: 380px;
        }
        & input.o-inputcontrol {
            height: 30px;
            box-sizing: inherit;
            width: 120px;
            min-width: 120px;
            max-width: 300px;
        }
    }
    &__searchtoggle {
        border-radius: 50%;
        margin-left: 19px;
        margin-right: 19px;
        width: 32px;
        display: none;
        &>i {
            color: $white;
        }
    }
    &__user {
        font-size: $font13;
        padding-right: 24px;
        flex-shrink: 0;
        margin-left: auto;
        // margin-left: 12px;
        display: flex;
        align-items: center;
        // min-width: 280px;
        & > span {
            cursor: default;
            max-width: 500px;
            display: block;
            word-break: break-all;
            width: auto;
            // max-width: 300px;
            word-wrap: break-word;
        }
        & .mdl-menu .mdl-menu__item.mdl-cell--hide-desktop{
             line-height: 24px;
             background-color: $lightgrey--light;
        }
        & .mdl-menu .mdl-menu__item.mdl-cell--hide-desktop:hover{
            background-color: $lightgrey--light;
            cursor: default;
        }
        & .mdl-menu .mdl-menu__item:last-child span{
            display: block;
        }        
    }
    &__support {
        padding-right: 24px;
        margin-left: 12px;
    }
    &.is-search {
        padding-right: 70px;
        & .header {
            &__menu {
                display: none;
            }
            &__logo {
                display: none;
            }
            &__search {
                margin-left: auto;
                margin-right: auto;
                & [device-type=mobile] {
                    display: none !important;
                }
                & [device-type=pc] {
                    display: block !important;
                }
            }
            &__searchtoggle {
                display: block;
            }
            &__user {
                display: none;
            }
            &__support {}
        }
    }
}

.headerbar {
    background-color: $puregrey;
    &__nav {
        & .mdl-list {
            display: flex;
            justify-content: center;
            padding: 0px;
        }
        & .mdl-list__item {
            border-bottom-style: solid;
            border-bottom-color: $puregrey;
            font-size: $font13;
            margin-right: 8px;
            transition: border-color 0.2s linear;
            overflow: visible;
            padding: 0px;
            &>a {
                padding: 16px;
                font-weight: 200;
            }
            & .mdl-list__item-icon,
            & .mdl-list__item-primary-content {
                color: $darkgrey;
            }
            & .mdl-list__item-icon {
                margin-right: 8px;
            }
            &:hover {
                cursor: pointer;
                border-bottom-color: $yblue;
                &>a>.mdl-list__item-primary-content,
                &>a>.mdl-list__item-primary-content>.mdl-list__item-icon {
                    color: $yblue;
                }
            }
            & .mdl-list__item-primary-content .mdl-list__item-icon {
                margin-right: 8px;
            }
        }
        & .mdl-submenu__productssupport {
            & .mdl-menu__container,
            & .mdl-menu__outline {
                width: 250px;
            }
            & .mdl-menu__container {
                left: -145px;
            }
        }
        & .mdl-submenu__description {
            & .mdl-menu__container,
            & .mdl-menu__outline {
                width: 250px;
            }
            & .mdl-menu__container {
                left: -146px;
            }
        }
        & .mdl-submenu__cloud {
            & .mdl-menu__container,
            & .mdl-menu__outline {
                width: 250px;
            }
            & .mdl-menu__container {
                left: -159px;
            }
        }
        & .mdl-list__item .mdl-menu__container {
            z-index: 999;
            right: 0px;
            top: 1px;
            & .mdl-menu__outline {
                opacity: 1;
                transform: scale(1);
                z-index: 999;
            }
            & .mdl-menu {
                opacity: 1;
                transform: scale(1);
                z-index: 999;
            }
            & .mdl-menu__item {
                opacity: 1;
                & a {
                    padding-left: 0px;
                    color: $darkgrey;
                    font-weight: 200;
                    font-size: 13px;
                    display: block;
                    width: 100%;
                    &:hover {
                        color: $yblue;
                    }
                }
            }
        }
        & .mdl-list__item:hover .mdl-menu__container {
            visibility: visible;
        }
        & .mdl-list__item.separate {
            padding: 0px;
            &>div {
                border-style: solid;
                height: 30px;
                border-width: 1px;
                border-color: $lightgrey--light;
                margin-top: 3px;
            }
        }
    }
    &__subnav {
        position: absolute;
    }
}

// brekpoint sample
////////// Phone ////////// 320
@media (max-width: $grid-tablet-breakpoint - 1) {
    .header {
        flex-wrap: nowrap;
        height: 56px;
        background-color: $yblue;
        &__menu {
            margin-right: 19px;
            &>i {
                color: $white;
            }
        }
        &__logo {
            display: none;
        }
        &__servicename {
            display: none;
        }
        &__title {
            color: $white;
            font-size: $font15;
        }
        &__search {
            margin: 0 0 0 auto;
            width: auto;
            & .o-inputgroup{
                margin-left: auto;
            }
            & input.o-inputcontrol {
                border-color: $white;
                min-width: 120px;
            }
            & .o-inputaddon {
                background-color: $yblue;
                border-color: $white;
                color: $white;
            }
            & button[device-type=mobile]{
                margin-left: auto;
            }            
        }
        &__support {
            & i,
            & span {
                color: $white;
            }
        }
        &.is-search {
            .header {
                &__title {
                    display: none !important;
                }
                &__support {
                    display: none !important;
                }
                &__search {
                    width: 100%;
                    max-width: 380px;
                    & .o-inputaddon {
                        box-sizing: content-box;
                        width: 32px;
                    }
                }
            }
        }
    }
}

