/* Scss Document */

@charset "utf-8";

/*

 webfontと丸を組み合わせたアイコン

*/

@mixin o-circleicon {
    border-radius: 50%;
    margin: auto;
    padding: 0;
    overflow: hidden;
    background: rgba(158, 158, 158, .2);
    position: relative;
    line-height: normal;
}

@mixin o-circleiconyblue {
    border-radius: 50%;
    margin: auto;
    padding: 0;
    overflow: hidden;
    background: $white;
    border-color: $yblue;
    border-style: solid;
    border-width: 3px;
    position: relative;
    line-height: normal;
    color: $yblue;
    font-weight: bold;
}

@mixin o-circleicon__icon {
    position: absolute;
    top: 50%;
    left: 50%;
}

@mixin o-iconlink {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    & > i + span {
        margin-left: 8px;
    }
}

.o-circleicon {
    &--sm {
        @include o-circleicon;
        height: 48px;
        width: 48px;
    }
    &--md {
        @include o-circleicon;
        height: 56px;
        width: 56px;
    }
    &--lg {
        @include o-circleicon;
        height: 64px;
        width: 64px;
    }
    &__icon {
        &--sm {
            @include o-circleicon__icon;
            transform: translate(-12px, -12px);
            font-size: 24px;
            line-height: 24px;
            width: 24px;
        }
        &--md {
            @include o-circleicon__icon;
            transform: translate(-15px, -15px);
            font-size: 30px;
            line-height: 30px;
            width: 30px;
        }
        &--lg {
            @include o-circleicon__icon;
            transform: translate(-18px, -18px);
            font-size: 36px;
            line-height: 36px;
            width: 36px;
        }
    }
    &label{
        display: flex;
        flex-direction: row;
        align-items: center;
        & > div {
            flex-glow: 0;
            flex-shrink: 0;
        }
        & > a{
            flex-glow: 0;
            flex-shrink: 0;
            margin-left: 24px;
            width: 245px;
        }
    }
}

.o-circleiconyblue {
    &--sm {
        @include o-circleiconyblue;
        height: 48px;
        width: 48px;
    }
    &--md {
        @include o-circleiconyblue;
        height: 56px;
        width: 56px;
    }
    &--lg {
        @include o-circleiconyblue;
        height: 64px;
        width: 64px;
    }
    &__icon {
        &--sm {
            @include o-circleicon__icon;
            transform: translate(-12px, -12px);
            font-size: 24px;
            line-height: 24px;
            width: 24px;
        }
        &--md {
            @include o-circleicon__icon;
            transform: translate(-15px, -15px);
            font-size: 30px;
            line-height: 30px;
            width: 30px;
        }
        &--lg {
            @include o-circleicon__icon;
            transform: translate(-18px, -18px);
            font-size: 36px;
            line-height: 36px;
            width: 36px;
        }
    }
}

.o-iconlink {
    @include o-iconlink;
    &--lightgreylight{
        @include o-iconlink;
        & > i , & > span {
            color: $lightgrey--light;
        }
    }   
}

.o-iconlabel {
    display: flex;
    flex-direction: row;
    color: $lightgrey--light;
    font-size: $font13;
    align-items: center;
    & > span {
        margin-left: 8px;
    }
}

@-moz-document url-prefix() {
    .o-iconlink > span{
        height: 24px;
    }
    .o-iconlabel > span {
        height: 24px;
        line-height: 20px;
    }
}