/* Scss Document */

@charset "utf-8";

@import "../object/component/_grid";
/* https://github.com/philipwalton/solved-by-flexbox/blob/master/assets/css/components/site.css */

.body {
    display: flex;
    flex-direction: column;
    height: 100%;
    margin-top: 59px;
    &__header {
        flex: none;
        position: fixed;
        top: 0px;
        width: 100%;
        z-index: 10000;
        flex-shrink: 0;
    }
    &__contents {
//        flex: 1 0 auto;
        margin-bottom: 140px;
    }
    &__footer {
//        flex: none;
        z-index: 10000;
        bottom: 0px;
        position: fixed;
        width: 100%;
        background-color: $puregrey;
    }
}


/* ログイン前 */

.contents {
//    display: flex;
//    flex-direction: row;
    height: 100%;
    &__main {
        padding-top: 40px;
        padding-left: 90px;
        padding-right: 90px;
        z-index: 1000;
        width: auto;
//        margin-right: auto;
//        margin-left: 292.5px;
        margin-right: auto;
        margin-left: auto;
        max-width: 1100px;
//        min-width: 500px;
//        flex: 1 1 auto;
        transition: margin-left 0.2s ease-out;	        
    }
  
    &__sidebar {
        padding-left: 0px;
        padding-right: 0px;
//        padding-top: 40px;
        z-index: 1000;
        width: 300px;
//        flex: 0 0 292.5px;
        position: fixed;
        transition: transform 0.2s;	
        transform: translateX(-300px);
        bottom: 109px;
        top: 59px;
        overflow-y: visible;
        background-color: $puregrey;
    }
    &__sidebar.is-show {
        transform: translateX(0);
        & + .contents__main{
//            transform: translateX(292.5px);
            display: block;
            margin-left: 300px;

        }
    }    

}


/* ログイン後 */

.contents--center {
    height: 100%;
    &__main {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 40px;
        z-index: 1000;
    }
}


////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .body:not(.is-sidebar-show){        
        & .contents{
            &__main {
                padding-left: 12px;
                padding-right: 12px;
            } 
        }
        
        & .contents--center {
            &__main {
                padding-top: 0px;
                padding-left: 12px;
                padding-right: 12px;                
            }
        }          
    }
    // .body {
    //     &__footer {
    //         position: static;
    //         z-index: 1;
    //     }
    // }
}

////////// Tablet //////////
@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {
    .body.is-sidebar-show{
        & .contents{
            &__main {
                padding-left: 12px;
                padding-right: 12px;
            } 
            
            &__sidebar.is-show {
                & + .contents__main{
                    margin-left: 0px;

                }
            }  
        }
    }
    
    .body:not(.is-sidebar-show){        
        & .contents{
            &__main {
                padding-left: 12px;
                padding-right: 12px;
            } 
        }

        & .contents--center {
            &__main {
                padding-top: 0px;
                padding-left: 12px;
                padding-right: 12px;                
            }
        }        
    }
    // .body {
    //     &__footer {
    //         position: static;
    //         z-index: 1;
    //     }
    // }    
}

////////// Desktop //////////
@media (min-width: $grid-desktop-breakpoint) and (max-width: $grid-menudesktop-breakpoint - 1) {
    .body.is-sidebar-show{
        & .contents{
            &__main {
                padding-left: 12px;
                padding-right: 12px;
            } 
        }
    }
}

