/* Scss Document */

@charset "utf-8";
@import "./_icon";
@import "./_color";
@import "./_grid";
html,
body {
    font-size: $font15;
}

ul,
ol {
    font-size: $font15;
}

.mdl-button{
    &--raised {
        background-color: $yblue;
        color: $white;
    }
    &--orange {
        background-color: $orange;
        color: $white;
    }
}

.mdl-button:hover{
    color: $black;
}

.mdl-checkbox{
	height: auto;
    width: auto;
}

.mdl-list__item {
    font-size: $font15;
}

.mdl-textfield {
    width: 100%;
    &__title {
        color: $lightgrey--light;
    }
    &__guide {
        color: $lightgrey--light;
        font-size: $font13;
        margin-top: -15px;
    }
    &__inputed {
        font-size: $font22;
    }
}

@mixin mdl-textfieldgroupcommon{
    display: flex;
    position: relative;
    &.is-invalid>.mdl-textfield__error {
        visibility: visible;
        top: 47px;
    }
    & .mdl-textfieldgroup--column+.mdl-textfieldgroup--column {
        margin-left: 20px;
    }
}

.mdl-textfieldgroupcolumn {
    @include mdl-textfieldgroupcommon;
    flex-direction: column;
}

.mdl-textfieldgrouprow {
    @include mdl-textfieldgroupcommon;
    flex-direction: row;
}

@mixin mdl-textfieldgroup {
    display: flex;
    &__item {
        width: 100%;
    }
    &__itemlabel {
        padding-top: 22px;
        padding-bottom: 4px;
        height: 29px;
    }
}

.mdl-textfieldgroup {
    @include mdl-textfieldgroup;
    flex-direction: row;
}

.mdl-textfieldgroup--column {
    @include mdl-textfieldgroup;
    flex-direction: column;
    width: 100%;
}

.mdl-switchgroup {
    display: flex;
    flex-direction: column;
    &__switch {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    &__guide {
        font-size: $font13;
        color: $lightgrey;
    }
}

.mdl-textfieldgroup__item+.mdl-textfieldgroup__item,
.mdl-textfieldgroup__itemlabel+.mdl-textfieldgroup__item,
.mdl-textfieldgroup__item+.mdl-textfieldgroup__itemlabel {
    margin-left: 16px;
}

.mdl-checkbox__box-outline {
    height: 19px;
    width: 19px;
}

.mdl-textfield--floating-label.is-focused .mdl-textfield__label,
.mdl-textfield--floating-label.is-dirty .mdl-textfield__label,
.mdl-textfield--floating-label.has-placeholder .mdl-textfield__label,
.mdl-textfield--floating-label.is-invalid.is-focused .mdl-textfield__label {
    font-size: $font15;
}

.mdl-textfieldgroup--column.is-invalid .mdl-textfield__guide,
.mdl-textfieldgrouprow.is-invalid+.mdl-textfield__guide {
    margin-top: 5px;
}

.mdl-textfield.is-invalid .mdl-textfield__input {
    border-color: $red;
}

.mdl-textfield--floating-label.is-invalid .mdl-textfield__label {
    color: $red;
    font-size: $font16;
}

.mdl-textfield.is-invalid .mdl-textfield__label:after {
    background-color: $red;
}

.mdl-textfield__error {
    color: $red;
    font-size: $font13;
}

.mdl-menu__item {
    white-space: normal;
    height: 100%;
}

// brekpoint sample
////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .mdl-textfield {
        width: 100%;
        &__title {
            font-size: $font13;
        }
        &__guide {
            font-size: $font13;
        }
        &__inputed {
            font-size: $font16;
            word-break: break-all;
        }
        &__label {
            font-size: $font13;
        }
        &__error {
            font-size: $font13;
            line-height: 16px;
        }
    }
    .mdl-checkbox{
        &__label {
            font-size: $font13;
        }        
    }
    
    .mdl-textfield--floating-label.is-focused .mdl-textfield__label,
    .mdl-textfield--floating-label.is-dirty .mdl-textfield__label,
    .mdl-textfield--floating-label.has-placeholder .mdl-textfield__label,
    .mdl-textfield--floating-label.is-invalid.is-focused .mdl-textfield__label,
    .mdl-textfield--floating-label.is-invalid .mdl-textfield__label{
        font-size: $font13;
    }
    
    .mdl-textfieldgroup__item+.mdl-textfieldgroup__item,
    .mdl-textfieldgroup__itemlabel+.mdl-textfieldgroup__item,
    .mdl-textfieldgroup__item+.mdl-textfieldgroup__itemlabel {
        margin-left: 4px;
    }
}

////////// Tablet //////////
@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {
    .mdl-textfield {
        width: 100%;
        &__title {
            font-size: $font13;
        }
        &__guide {
            font-size: $font13;
        }
        &__inputed {
            font-size: $font16;
            word-break: break-all;
        }
        &__label {
            font-size: $font13;
        }
        &__error {
            font-size: $font13;
            line-height: 16px;
        }
    }
    .mdl-checkbox{
        &__label {
            font-size: $font13;
        }        
    }
    
    .mdl-textfield--floating-label.is-focused .mdl-textfield__label,
    .mdl-textfield--floating-label.is-dirty .mdl-textfield__label,
    .mdl-textfield--floating-label.has-placeholder .mdl-textfield__label,
    .mdl-textfield--floating-label.is-invalid.is-focused .mdl-textfield__label,
    .mdl-textfield--floating-label.is-invalid .mdl-textfield__label{
        font-size: $font13;
    }
    
    .mdl-textfieldgroup__item+.mdl-textfieldgroup__item,
    .mdl-textfieldgroup__itemlabel+.mdl-textfieldgroup__item,
    .mdl-textfieldgroup__item+.mdl-textfieldgroup__itemlabel {
        margin-left: 4px;
    }
}

////////// Desktop //////////
@media (min-width: $grid-desktop-breakpoint) and (max-width: $grid-menudesktop-breakpoint - 1) {
    .body.is-sidebar-show {
        .mdl-textfield {
        width: 100%;
        &__title {
            font-size: $font13;
        }
        &__guide {
            font-size: $font13;
        }
        &__inputed {
            font-size: $font16;
            word-break: break-all;
        }
        &__label {
            font-size: $font13;
        }
        &__error {
            font-size: $font13;
            line-height: 16px;
        }
        }
        .mdl-checkbox{
            &__label {
                font-size: $font13;
            }        
        }

        .mdl-textfield--floating-label.is-focused .mdl-textfield__label,
        .mdl-textfield--floating-label.is-dirty .mdl-textfield__label,
        .mdl-textfield--floating-label.has-placeholder .mdl-textfield__label,
        .mdl-textfield--floating-label.is-invalid.is-focused .mdl-textfield__label,
        .mdl-textfield--floating-label.is-invalid .mdl-textfield__label{
            font-size: $font13;
        }
        
        .mdl-textfieldgroup__item+.mdl-textfieldgroup__item,
        .mdl-textfieldgroup__itemlabel+.mdl-textfieldgroup__item,
        .mdl-textfieldgroup__item+.mdl-textfieldgroup__itemlabel {
            margin-left: 8px;
        }
    }
}
