/* Scss Document */

@charset "utf-8";
.o-card {
    max-width: 465px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-style: solid;
    border-color: $lightgrey--light;
    border-width: 1px;
    border-radius: 5px;
    background-color: $white;
    padding-right: 40px;
    padding-left: 40px;
    padding-top: 40px;
    padding-bottom: 40px;
    box-sizing: border-box;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
    margin-left: auto;
    margin-right: auto;
    & input:not(.mdl-textfield__input),
    & select {
        font-size: $font17;
        height: 60px;
    }
    & form {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    &__header {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 36px;
        & .o-card__headerleft.mdl-button {
            padding-right: 0px;
            padding-left: 0px;
            min-width: 36px;
            border-radius: 50%;
        }
        & .o-card__title {
            font-size: $font33;
            color: $yblue;
            line-height: 33px;
        }
        & .o-card__headerleft {
            &--blank {
                width: 36px;
            }
        }
        & .o-card__headerrigt {
            &--blank {
                width: 36px;
            }
        }
    }
    &__message {
        font-size: $font15;
        width: 100%;
        color: $darkgrey;
        & a {
            text-decoration: underline;
            color: $yblue;
            & __center {
                text-align: center;
            }
        }
        & span {
            word-break: break-all;
        }
    }
    &__btn {
        color: $white;
        width: 100%;
        background-color: $yblue;
        font-size: $font21;
        font-weight: bold;
        height: 60px;
    }
    &--menu {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        max-width: 684px;
        justify-content: space-between;
    }
    &menu__item {
        // flex: 0 1 320px;
        flex-shrink: 1;
        max-width: 330px;
        min-height: 168px;
        height: 100%;
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
        border-color: $lightgrey--light;
        padding: 24px;
        margin-bottom: 24px;
        width: 100%;
        // margin-left: 24px;
        box-sizing: border-box;
        & .o-cardmenuitem {
            &--message {
                width: 100%;
                color: $darkgrey;
                font-size: $font15;
            }
        }
        & .o-circleicon--sm {
            margin: initial;
            background-color: $yblue;
            &>i {
                color: $white;
            }
        }
        & .o-circleiconyblue--sm {
            margin: initial;
        }
        & .o-circleiconlabel > a{
            flex-shrink: 1;
            width: 248px;
        }

    }
}

// brekpoint sample
////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .o-card {
        border-style: none;
        box-shadow: none;
        padding: 40px 0px 0px 0px;
        &__header {
            &.is-mobile-visible {
                display: flex!important;
                & .o-card {
                    &__title {
                        font-size: $font24;
                    }
                }
            }
            &:not(.is-mobile-visible) {
                & .o-card {
                    &__title {
                        font-size: $font17;
                    }
                    &__headerrigt--blank,
                    &__headerleft--blank {
                        display: none;
                    }
                }
            }
        }
        &__btn {
            height: 39px;
            font-size: $font13;
        }
        &__message {
            font-size: $font13;
        }
        & input:not(.mdl-textfield__input),
        & select {
            font-size: $font13;
            height: 47px;
        }
        &--menu {
            max-width: 659px;
        }
        &menu__item {
            margin-right: auto;
            margin-left: auto;
        }
    }
}

